/*styles*/

html,body{
  font-family: 'Ubuntu Mono', monospace, sans-serif;
  box-sizing: border-box;
  min-height: 100%;
  min-width: 100%;
}

@media only screen and (max-width:600px) {
  html,body{
    font-size:14.5px;
  }  
}
@media only screen and(min-width:600px){
  html,body{
    font-size:15.5px;
  }
}
@media only screen and(min-width:800px){
  html,body{
    font-size:16.5px;
  }
}
@media only screen and (min-width:1000px){
  html,body{
    font-size:17.5px;
  }
}

.App{
  min-width: 100%;
  min-height:100vh;
}

.theme{
  width:-webkit-fit-content;
  width:-moz-fit-content;
  width:fit-content;
  background-color: #ddd;
  color: black;
}
.theme-dark{
  width:-webkit-fit-content;
  width:-moz-fit-content;
  width:fit-content;
  background-color: #222;
  color:#999;
}

.top{
  box-sizing: border-box;
  height:15vh;
  min-height:100px;
  max-height:120px;
  width: 100%;
  border-bottom:1px solid #bbb;
  text-align: center;
}

.columns{
  margin:0px;
  display:flex;
  box-sizing: border-box;
  min-height:70vh;
}

.left{
  width:15%;
  min-width:170px;
  max-width:220px;
  border-right: 1px solid #bbb;
}

.center{
  min-height: 480px;
  width:70%;
  box-sizing: border-box;
  border-right: 1px solid #bbb;
  min-width:870px;
}

.bottom{
  height: 10vh;
  min-height:70px;
  width: 100%;
  border-top:1px solid #bbb;
  text-align: center;
  margin: 0;
  padding: 0;
}

.bottom-text{
  font-family: 'Mulish',Arial, Helvetica, sans-serif;
  margin-top:8px;
}

.bottom-mailid{
  color:darkcyan;
  text-decoration:none;
  margin-left:0.2rem;
}
.bottom-mailid:hover{
  text-decoration: underline;
}



